@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #b1ede8;
  font-family: "Montserrat", sans-serif;
}
.App {
  text-align: center;
  background-color: #333;
  width: 600px;
  min-height: 800px;
  padding: 10px;
  border-radius: 15px;
  border: 5px solid white;
}
.App h1 {
  padding: 20px;
  color: white;
}
.App input {
  height: 50px;
  width: 300px;
  font-size: 20px;
  padding: 5px;
  color: white;
  background: transparent;
  border: none;
  border-bottom: 3px solid white;
}
button.add {
  width: 80px;
  height: 50px;
  background-color: transparent;
  color: white;
  border: none;
  border-bottom: 3px solid white;
  font-size: 20px;
  transition: 0.3s ease-in linear;
}
button.add:hover {
  cursor: pointer;
  color: greenyellow;
}
p.byAh {
  float: right;
  font-size: 24px;
}

button.deleteBtn,
button.completeBtn {
  background: transparent;
  border: none;
  margin: 10px;
  color: white;
  font-size: 16px;
  border-bottom: 2px solid white;
  cursor: pointer;
}
button.deleteBtn:hover {
  border-bottom: 2px solid red;
}
button.completeBtn:hover {
  border-bottom: 2px solid greenyellow;
}
.complete {
  text-decoration: line-through;
}
div.todoContainer {
  border: 3px solid white;
  padding: 0;
  margin: 15px;
  border-radius: 13px;
  font-size: 12px;
  -webkit-animation: drop 1.2s;
          animation: drop 1.2s;
}
@-webkit-keyframes drop {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes drop {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

